export default {
  name: 'FileUploader',
  props: ['type', 'url', 'width', 'height', 'progress', 'extra'],
  data() {
    return {};
  },
  mounted() {},
  components: {},
  computed: {
    parseExtra() {
      return JSON.parse(this.extra);
    },
    fileSize() {
      if (!this.parseExtra.size) {
        return 0;
      }
      let bytes = this.parseExtra.size;
      let thresh = 1024;
      if (Math.abs(bytes) < thresh) {
        return bytes + ' B';
      }
      let units = ['kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
      var u = -1;
      do {
        bytes /= thresh;
        ++u;
      } while (Math.abs(bytes) >= thresh && u < units.length - 1);
      return bytes.toFixed(1) + ' ' + units[u];
    }
  },
  methods: {}
};



